import {
  Home as HomeIcon,
  CollectionsOutlined as GalleryIcon,
  CodeOutlined as CodeIcon,
  Gesture as GestureIcon,
  AccountBoxRounded as UserIcon,
  SettingsOutlined as SettingsIcon,
  ListAlt as ListIcon,
  CreditCard as BillingIcon,
} from '@material-ui/icons';

import { Canvas } from '../pages/Canvas';
import { Gallery } from '../pages/Gallery';
import { Home } from '../pages/Home';
import { Route } from '../types/Route';

const routes: Array<Route> = [
  {
    key: 'router-home',
    title: 'Home',
    description: 'Home',
    component: Home,
    path: '/',
    isEnabled: true,
    icon: HomeIcon,
    appendDivider: true,
  },
  {
    key: 'router-drawing',
    title: 'Canvas',
    description: 'Create a new canvas',
    path: '/canvas/*',
    component: Canvas,
    isEnabled: true,
    icon: GestureIcon,
  },
  {
    key: 'router-gallery',
    title: 'Gallery',
    description: 'Gallery',
    path: '/gallery',
    component: Gallery,
    isEnabled: true,
    icon: GalleryIcon,
  },
  {
    key: 'router-code',
    title: 'Code Editor',
    description: 'Code Editor',
    path: '/code-editor',
    isEnabled: true,
    icon: CodeIcon,
    appendDivider: true,
  },
  {
    key: 'router-my-account',
    title: 'My Account',
    description: 'My Account',
    path: '/account',
    isEnabled: true,
    icon: UserIcon,
    subRoutes: [
      {
        key: 'router-settings',
        title: 'Settings',
        description: 'Account Settings',
        path: '/account/settings',
        isEnabled: true,
        icon: SettingsIcon,
      },
      {
        key: 'router-preferences',
        title: 'Preferences',
        description: 'Account Preferences',
        path: '/account/preferences',
        isEnabled: true,
        icon: ListIcon,
      },
      {
        key: 'router-billing',
        title: 'Billing',
        description: 'Account Billing',
        path: '/account/billing',
        isEnabled: true,
        icon: BillingIcon,
      },
    ],
  },
];

export default routes;