import styled from "@emotion/styled"
import { Button, Input, Paper, Slider, TextField, Typography } from "@mui/material"
import { useState } from "react";


const Creation = () => {
    const [nameInput, setNameInput] = useState<string>("");
    const [descriptionInput, setDescriptionInput] = useState<string>("");
    const [widthInput, setWidthInput] = useState<number>(500);
    const [heightInput, setHeightInput] = useState<number>(500);

    const onNameChange = (e: any) => setNameInput(e.target.value);
    const onDescriptionChange = (e: any) => setDescriptionInput(e.target.value);
    const onWidthChange = (e: any) => setWidthInput(e.target.value);
    const onHeightChange = (e: any) => setHeightInput(e.target.value);

    return (
        <Container>
            <Typography variant="h4">{`Create a new canvas`}</Typography>
            <hr />
            <br />
            <Paper style={{ 'minHeight': '60vmin', padding: '2em' }}>
                <TextField
                    style={{ 'width': '100%', paddingBottom: '1em' }}
                    onChange={onNameChange}
                    value={nameInput}
                    label={"Name"}
                    placeholder={"Enter an appropriate name."}
                    required={true}
                />
                <TextField
                    style={{ 'width': '100%', paddingBottom: '1em' }}
                    onChange={onDescriptionChange}
                    value={descriptionInput}
                    label={"Description"}
                    placeholder={"Enter a description for your canvas."}
                />
                <SliderContainer>
                    <DimensionLabel>Width</DimensionLabel>
                    <Input type="number" value={widthInput} onChange={onWidthChange} />
                    <Slider min={0} max={2000} aria-label="Default" valueLabelDisplay="auto"
                        value={widthInput} onChange={onWidthChange} />
                </SliderContainer>
                
                <SliderContainer>
                    <DimensionLabel>Height</DimensionLabel>
                    <Input type="number" value={heightInput} onChange={onHeightChange} />
                    <Slider min={0} max={2000} aria-label="Default" valueLabelDisplay="auto"
                        value={heightInput} onChange={onHeightChange} />
                </SliderContainer>

                <Button
                    style={{ border: 'solid', marginBottom: '1em' }}
                >Submit</Button>
            </Paper>
        </Container>
    )
}

const Container = styled('div')`
    flex: 1;
    text-align: center;
    padding: 5em;
`

const SliderContainer = styled('div')`
    display: flex;
    flex-direction: row;
    align-items: center;
`

const DimensionLabel = styled('p')`
    padding: 1em;
    > * {
        padding: 1em;
    };
`

export default Creation