import { Helmet } from 'react-helmet';
import { Route, Routes } from 'react-router-dom';
import Creation from '../components/Session/Canvas/Creation';
import { Edition } from '../components/Session/Canvas/Edition';


export const Canvas = () => {

  return (
    <div id="content-wrapper">
      <Helmet>
        <title>
          Canvas
        </title>
      </Helmet>
      <Routes>
        <Route path=":id" element={<Edition />} />
        <Route path="*" element={<Creation />} />
      </Routes>
    </div>
  );
};
