import styled from "@emotion/styled";
import { StatusCodes } from "http-status-codes";
import { useState, useEffect } from "react";
import { useModal } from "../../../../hooks";
import CanvasService from "../../../../services/canvas/CanvasService";
import { Drawing } from "../../../../types/Drawing";
import { Popup } from "../../Popup";
import PreviewPopup from "../../Popup/PreviewPopup";
import { Preview } from "../../Preview";

export const Drawings = () => {
    const [drawings, setDrawings] = useState(null as (Drawing[] | null));
    const [selectedDrawing, setSelectedDrawing] = useState(null as (Drawing | null));
    const { isShown: showPreviewPopup, toggle: togglePreviewPopup } = useModal();

    function handleDrawingClick(drawing: Drawing) {
        togglePreviewPopup();
        setSelectedDrawing(drawing);
    }

    useEffect(() => {
        const fetchDrawings = async () => {
            const response = await CanvasService.getAllCanvas();
            if (response.status === StatusCodes.OK) {
                const data = response.data as Drawing[];
                setDrawings(data)
            }
        }

        if (!drawings) {
            fetchDrawings();
        }
    }, [drawings])

    return (
        <div>
            <DrawingsContainer>
                {drawings ?
                    drawings.map(drawing =>
                        <Preview title={drawing.name} author={drawing.author} togglePopup={() => handleDrawingClick(drawing)} />
                    ) : null
                }
            </DrawingsContainer>

            {selectedDrawing ?
                <Popup
                    isShown={showPreviewPopup}
                    hide={togglePreviewPopup}
                    headerText={selectedDrawing.name}
                    modalContent={<PreviewPopup id={selectedDrawing._id} />}
                /> : null
            }

        </div>
    );
};

const DrawingsContainer = styled('div')`
    display: flex;
    flex-wrap: wrap;
    > * {
        margin: .5em;
    };
`